import { Component, OnInit, Input, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Ticker, TickersNews } from '../models/news';

import { Store } from '@ngrx/store';
import * as newsActions from '../store/news.actions';

@Component({
  selector: 'app-add-ticker-dialog',
  templateUrl: './add-ticker-dialog.component.html'
})
export class AddTickerDialogComponent implements OnInit {
  onAdd = new EventEmitter();

  ticker = new Ticker();
  @Input() public dhivehiEnabled = true;

  constructor(
    public dialogRef: MatDialogRef<AddTickerDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private store: Store<any>,
  ) { }

  ngOnInit() {
    this.ticker.color = '#000000';
    this.ticker.enabled = 1;
  }

  closeDialog(confirmation: boolean) {
    this.dialogRef.close(confirmation);
  }

  doAdd() {
    this.closeDialog(true);
    //this.onAdd.emit(this.tickerNews);
    this.store.dispatch(new newsActions.AddTicker(this.ticker));
    this.onAdd.emit();
  }

}
