import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable, of, BehaviorSubject } from 'rxjs';

import { environment } from './../../../environments/environment';
import { News, TickersNews, Ticker } from './../models/news';

@Injectable({
  providedIn: 'root'
})
export class NewsService {
  private apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient
  ) { }

  //get dashboard data
  getDashboard () {
    return this.http.get<any>(this.apiUrl + 'news/dashboard');
  }

  // get the news that belongs to me, with status & bar(s)
  myNews (perpage: number = 10, page: number = 1) {
    return this.http.get<any>(this.apiUrl + 'news', {
      params: new HttpParams()
        .set('no_result', perpage.toString())
        .set('page', page.toString())
    });
  }

  // get the trashed news that belongs to me, with status & bar(s)
  trashNews (perpage: number = 10, page: number = 1) {
    return this.http.get<any>(this.apiUrl + 'news/trash', {
      params: new HttpParams()
        .set('no_result', perpage.toString())
        .set('page', page.toString())
    });
  }

  // get a news, with status & bar(s)
  getNews (id: number) {
    return this.http.get<any>(this.apiUrl + 'news/' + id);
  }

  // save a news, with bar(s) & ticker(s)
  saveNews (save_news: News) {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});

    save_news.newsstatus_id = 1;
    const post_str = JSON.parse(JSON.stringify(save_news));
    console.log(post_str);

    return this.http.post<any>(this.apiUrl + 'news', post_str, { headers: headers });
  }

  // untrash a news
  unTrashNews (id: number) {
    return this.http.put<any>(this.apiUrl + 'news/untrash/' + id, null);
  }

  // deleate a news
  destroyNews (id: number) {
    return this.http.delete<any>(this.apiUrl + 'news/' + id);
  }

  // deleate a news
  destroyNewsForever (id: number) {
    return this.http.delete<any>(this.apiUrl + 'news/destroy/' + id);
  }

  // deleate a news bar
  destroyBar (id: number) {
    return this.http.delete<any>(this.apiUrl + 'news/bar/' + id);
  }

  // get a news for editing
  editNews (id: number) {
    return this.http.get<any>( this.apiUrl + 'news/edit/' + id );
  }

  // save a news, with bar(s) & ticker(s)
  updateNews (save_news: News, id: number) {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});

    save_news.newsstatus_id = 1;
    const post_str = JSON.parse(JSON.stringify(save_news));

    return this.http.put<any>(this.apiUrl + 'news/' + id, post_str, { headers: headers });
  }

  // Get tickers, tickers are returned with ticker news
  GetTickers () {
    return this.http.get<any>(this.apiUrl + 'news/ticker/tickers');
  }

  // Add a news to a ticker
  AddTickerNews (ticker_news: TickersNews) {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});

    return this.http.post<any>(this.apiUrl + 'news/ticker/news/add', ticker_news, { headers: headers });
  }

  // Enable a ticker news
  ApproveTickerNews (ticker_news_id: number) {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});

    return this.http.put<any>(this.apiUrl + 'news/ticker/news/enable', { ticker_news_id: ticker_news_id }, { headers: headers });
  }

  // Disable a ticker news
  DisApproveTickerNews (ticker_news_id: number) {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});

    return this.http.put<any>(this.apiUrl + 'news/ticker/news/disable', { ticker_news_id: ticker_news_id }, { headers: headers });
  }

  // deleate a ticker news
  destroyTickerNews (ticker_news_id: number) {
    return this.http.delete<any>(this.apiUrl + 'news/ticker/news/' + ticker_news_id);
  }


  // Add a ticker
  AddTicker (ticker: Ticker) {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});

    return this.http.post<any>(this.apiUrl + 'news/ticker/add/new', ticker, { headers: headers });
  }
}
